.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 35rem;
  height: 30rem;
  padding: 2rem;
  background-color: #fff;
  border-radius: 2rem;
  margin-bottom: 5rem;
  box-shadow: 20px 20px 15px -3px rgba(0, 0, 0, 0.1);
  margin-right: 5rem;

  & p {
    font-size: 2.5rem;
  }

  &:not(:last-child) {
    // margin-right: 5rem;
  }

  & h2 {
    font-size: 1.8rem;
  }

  & button {
    width: 5rem;
    height: 3rem;
    border: none;
    background-color: #b40c0c;
    color: #fff;
    border-radius: 1rem;
    border: 0.2rem solid #000;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 5rem;
    }
    // position: absolute;
    // top: 115%;
    // left: 30%;
  }
}

.btn {
  display: flex;
}

@media (max-width: 550px) {
  .product {
    min-width: 45rem;
    height: 35rem;

    & button {
      width: 8rem;
      height: 5rem;
    }

    & p {
      font-size: 3rem;
    }
  }
}

@media (max-width: 340px){
    .product {
        min-width: 55rem;
        height: 45rem;
    
        & button {
          width: 10rem;
          height: 7rem;
        }
    
        & p {
          font-size: 5rem;
        }
      }
}